.shopItems .card {
  transition: 0.3s;
}

@media (min-width: 767px) {
  .shopItems .card:hover {
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
  }
}

.shopItems .card .size.active {
  background-color: #17a2b8;
}

.shopItems .miniImage.active {
  border: 2px solid #17a2b8 !important;
}
